import React, { Component } from 'react';
import Table from 'antd/lib/table'
import 'antd/lib/table/style/index.css'
import 'antd/lib/pagination/style/index.css'
import 'antd/lib/icon/style/css.js'
const columns = [{
  title: 'ID',
  dataIndex: 'id'
}, {
  title: '姓名',
  dataIndex: 'name'
}, {
  title: '手机号码',
  dataIndex: 'phone'
}, {
  title: '性别',
  dataIndex: 'sex',
  render: sex => {
    sex = parseInt(sex);
    if(sex === 1){
      return '男';
    }else if(sex === 2){
      return '女';
    }
    return '未知';
  }
} ,{
  title: '地址',
  dataIndex: 'address'
},{
  title: '留言',
  dataIndex: 'remark',
  render: remark => <span style={{color: '#f81d22' }}>{remark}</span>
}, {
  title: '创建时间',
  dataIndex: 'create_time'
}]
class App extends Component {
  constructor(props){
    super(props);
    this.getData = this.getData.bind(this);
    this.changePage = this.changePage.bind(this);
    this.state = {
      data: [],
      total: 0,
      current: 1,
      totalPage: 1
    }
  }
  render() {
    let { total, data, current, totalPage } = this.state;
    return (
      <Table rowKey='id' dataSource={data} columns={columns} pagination={{pageSize: 20,current: current,onChange: this.changePage,total: total, showTotal: (total, range) => `${total} 条记录 ${current}/${totalPage}页`}}/> 
    );
  }
  componentDidMount(){
    this.getData();
  }
  changePage(page){
    this.setState({
      current: page
    })
  }
  getData(){
    var xml = new XMLHttpRequest();
    xml.open('get', '/message/list'); 
    xml.onreadystatechange = () => {
      if(xml.status === 200 && xml.readyState === 4){
        // let data = JSON.parse('{"error_code":0,"message":"获取成功","data":[{"id":"1","name":"王二小","phone":"18211111111","address":"这是地址","remark":"如果有其它需求，请在此说明","create_time":"2018-08-07 14:05"}]}').data;
        let data = JSON.parse(xml.responseText).data.reverse();
        this.setState({
          data,
          total: data.length,
          totalPage: Math.ceil(data.length / 20)
        })
      }
    }
    xml.send();
  }
}

export default App;



